import popupLayer from '../../components/popup-layer/popup-layer.vue'
export default {
    components: {
       popupLayer
    },
    data(){
        return {
            show: false, // 遮罩层
            wl_show: false, // 物流选择
            checked: true,
            checked1: true,
            checked2: true,
            checked3: true,
        }
    },
    methods: {
        // 点击遮罩
        showOverlay(){
            this.show = false;
            this.wl_show = false;
        },
		submit(){
			this.$refs.popupLayer.show();
		}
    }
}
